import "src/styles/tailwind.css" // Kept existing import for Tailwind styles

import { motion } from 'framer-motion'; // Added: Framer Motion for smooth fill animations and level-up effects (install: pnpm add framer-motion; lightweight for transitions)
import { Tooltip } from '@/components/Tooltip/Tooltip'; // Added: Your custom Tooltip for lore placeholders on hover (game feel)
import { Progress } from '@/components/ui/progress'; // Added: Shadcn Progress component for visual XP bar (install if not: npx shadcn-ui@latest add progress)


export default async function RootLayout({ children }: { children: React.ReactNode }) {
  const userXp = 150; // Kept stubbed XP for testing without auth (Level 1, 50/100 XP); replace with session.user?.xp when adding auth
  const currentLevel = Math.min(Math.floor(userXp / 100), 99); // Kept existing level calc (100 XP per level placeholder—adjust to your defined task values; no multipliers as per your request)
  const progressToNext = userXp % 100; // Added: Calc progress % to next level for bar fill (0-100)

  return (
    <html lang="en">
      <body>
        {/* Updated: Persistent XP bar on every page (visual progress, like WoW top bar; added animations/styling/tooltip/responsiveness for cooler feel) */}
        <Tooltip explain="[Placeholder Lore] Your XP to next level—complete quests to fill it!">
          <motion.div 
            className="fixed top-0 left-0 w-full h-6 bg-gray-800 border-b border-gold-500 z-50 flex items-center justify-center md:h-8" // Added: Game-themed styling (dark bg, gold border for MMORPG feel); responsive height for mobile
            initial={{ opacity: 0, y: -20 }} // Added: Framer animation—fade/slide in on load
            animate={{ opacity: 1, y: 0 }} // Added: Smooth transition for bar appearance
            transition={{ duration: 0.5 }} // Added: 0.5s duration for animation
          >
            <Progress value={progressToNext} className="w-3/4 h-2 bg-gray-600" /> {/* Updated: Progress to next level with Tailwind styling (green fill by default; customize in shadcn theme) */}
            <span className="absolute text-xs text-white md:text-sm">Level {currentLevel} ({progressToNext}/100 XP)</span> /* Updated: Level display with placeholder for lore tooltip (hover for explanation) */
          </motion.div>
        </Tooltip>
        {children} // Kept existing children render
      </body>
    </html>
  )
}